<template>
    <div class="shopcar">
        <van-nav-bar
        title="购物车"
        left-text="返回"
        :right-text="isEdit ? '编辑' : '完成'"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
        />
        <div class="swip">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item><img src="../assets/images/bann_01.png" alt=""></van-swipe-item>
                <van-swipe-item><img src="../assets/images/bann_02.png" alt=""></van-swipe-item>
                <van-swipe-item><img src="../assets/images/bann_03.png" alt=""></van-swipe-item>
                <van-swipe-item><img src="../assets/images/bann_04.png" alt=""></van-swipe-item>
            </van-swipe>
        </div>
     

        <van-empty v-if="isCar" description="您还没有添加购物车哦！">
            <van-button round  class="bottom-button" @click="goMenu">
            逛一逛
            </van-button>
        </van-empty>


        <div class="van-swipe-box"  v-else >
            <div class="shop-item" v-for="(item,index) in shopbagData" :key="item.sid">
                <van-swipe-cell :disabled="isEdit">
                    <div class="left-box">
                        <van-checkbox v-model="item.isChecked" @click="simpleSelect"></van-checkbox>
                        <div class="shop-item-info">
                                    <div class="shop-item-img">
                                        <img  class="auto-img" :src="item.small_img" alt="">
                                    </div>
                                    <div class="shop-item-desc">
                                        <div class="shop-name">
                                            <div>{{item.name}}</div> 
                                            <div class="condit">{{item.rule}}</div>
                                        </div>
                                        <div class="ename">{{item.enname}}</div>
                                        <div class="shopPrice-box">
                                            <div class="price">{{item.price}}</div>
                                            <van-stepper theme="round" button-size="20" v-model="item.count" @change="changeCount(item)"/>
                                        </div>
                                    </div>
                        </div>
                    </div>
                    <template #right>
                        <van-button square type="danger" text="删除" @click.stop="deleteShopCar(index,item.sid)" />
                    </template>
                </van-swipe-cell>
            </div>

        </div>
        <van-submit-bar :price="total" :button-color="isEdit ? 'red' : 'blue'" v-if="isEdit" button-text="提交订单" @submit="onSubmit">
            <van-checkbox v-model="isAllChecked" @click.stop="allSelect">全选</van-checkbox>
        </van-submit-bar>
        <van-submit-bar :price="total" :button-color="isEdit ? 'red' : 'blue'" v-else button-text="删除选择" @submit="delSelect">
            <van-checkbox v-model="isAllChecked" @click.stop="allSelect">全选</van-checkbox>
        </van-submit-bar>
    </div>
</template>

<script>
import '../assets/less/shopcar.less'
    export default {
        data(){
            return {
                isEdit: true,
                isCar: false,
                radio: '',
                value: 1,
                //购物袋数据
                shopbagData: [],
                loading: true,
                isAllChecked: false,
                total: 0
                
            }
        },
        created() {
            //获取购物袋数据
            this.getShopbagData();
            
        },
        methods: {
            onClickLeft(){
                this.$router.go(-1);
            },
            onClickRight(){
                this.isEdit = !this.isEdit
            },
            //获取购物袋数据
            getShopbagData() {
                //获取token
                let tokenString = localStorage.getItem("token");
                if (!tokenString) {
                    //跳回登录页面
                    this.$toast("请先登录");
                    return this.$router.push({ name: "Login" });
                }

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                });

                // 发起获取购物袋数据请求
                this.axios({
                    method: "GET",
                    url: "/findAllShopcart",
                    params: {
                    appkey: this.appkey,
                    tokenString,
                    },
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 700){
                        this.$router.push({name: 'Login'});
                    }else if(res.data.code == 5000){
                        res.data.result.map(v => {
                            v.isChecked = false;
                        })
                    }
                    this.shopbagData = res.data.result;
                    
                
                    if(this.shopbagData.length == 0){
                        this.isCar =  true;
                    }
                    this.loading = false;
                }).catch(err => {
                    this.$toast.clear();
                })
            }, 
            //删除单个购物车数据
            deleteShopCar(index,sid){
                let tokenString = localStorage.getItem('token');
                let sid_ = [];
                sid_.push(sid)
                let sids = JSON.stringify(sid_ );

                if(!tokenString){
                    this.$toast("请登录");
                    return this.$router.push({name: 'Login'});
                }
                
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                });
                this.axios({
                    method: 'POST',
                    url: '/deleteShopcart',
                    data:{
                        appkey: this.appkey,
                        tokenString,
                        sids,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if (res.data.code == 700) {
                         //token检验无效,则跳到登录页面
                        this.$router.push({ name: "Login" });
                    }else if(res.data.code == 7000){
                        this.shopbagData.splice(index,1);
                        this.sum();
                    }
                    this.$toast(res.data.msg);
                    this.getShopbagData();

                }).catch(err => {
                    this.$toast.clear();
                })
            },
            //多选删除
            delSelect(){
                let tokenString = localStorage.getItem('token');

                if(!tokenString){
                    this.$toast("请登录");
                    return;
                }
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                });
                let selectedData = [];
                this.shopbagData.map(v => {
                    if(v.isChecked){
                        selectedData.push(v.sid);
                    }
                })
                let sids = JSON.stringify(selectedData);
                this.axios({
                    method: 'POST',
                    url: '/deleteShopcart',
                    data:{
                        appkey: this.appkey,
                        tokenString,
                        sids,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 700){
                        this.$router.push({name: 'Login'});
                    }else if(res.data.code == 7000){
                       for(let i = 0; i < this.shopbagData.length; i++){
                           if(this.shopbagData[i].isChecked){
                               this.shopbagData.splice(i,1);
                               i--;
                           }
                       }
                        this.sum();   
                        this.getShopbagData();
                    }
                    this.$toast(res.data.msg);
                }).catch(err => {
                    this.$toast.clear();
                })
            },
            // 单选
            simpleSelect(){
                this.sum();
                for(let i = 0; i < this.shopbagData.length; i++){
                    if(!this.shopbagData[i].isChecked){
                        this.isAllChecked = false;
                        return;
                    }
                }
                this.isAllChecked = true;
            },
            //全选
            allSelect(){
                this.shopbagData.map(v => {
                    v.isChecked = this.isAllChecked;
                })  
                this.sum();             
            },
            //修改数量
            changeCount(item){
               let tokenString = localStorage.getItem('token');
               if(!tokenString){
                   this.$toast("请先登录");
                   return this.$router.push({ name: "Login" });
               } 
               
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                });

               this.axios({
                   method: 'POST',
                   url: '/modifyShopcartCount',
                   data:{
                        appkey: this.appkey,
                        sid: item.sid,
                        count: item.count,
                        tokenString,
                   }
               }).then(res => {
                   this.$toast.clear();
                   if(res.data.code == 700){
                       this.$toast.push({name: 'Login'});
                   }else if(res.data.code == 6000){
                       if(item.isChecked){
                           this.sum();
                       }
                   }else{
                       this.$toast(res.data.msg);
                   }
               }).catch(err => {
                   this.$toast.clear();
               })
            },
            // 订单总金额
            sum(){
                this.total = 0;
                this.shopbagData.map(v => {
                    if(v.isChecked){
                        this.total += v.price * v.count;
                    }
                })
                this.total *= 100;
            },
            //提交订单
            onSubmit(){
                let selectedData = [];
                this.shopbagData.map(v => {
                    if(v.isChecked){
                        selectedData.push(v.sid);
                    }
                })
                if(selectedData.length == 0){
                    return;
                }else{
                    this.$router.push({name: 'Order',query:{sids:selectedData.join('-')}})
                }
            },
            goMenu(){
                this.$router.push({name: 'Menu'});
            }
           
        }
    }
</script>

<style lang="less" scoped>
     /deep/.van-swipe{
            height: 160px;
        }
</style>